@import 'variable';
@import 'common';
@import 'extend';
@import 'mixins';


//start-nav-section
#navbar {
	background-color: $nav-bg;
	min-height: 60px;

	nav{
		.logo {
			float: left;
			padding: 8px;
			margin-left: 16px;
			margin-top: 8px;
		}
		.nav-wrapper {
			overflow: hidden;
			margin-top: -20px !important;
		}
		ul{
			float: right;

			li {
				display: inline-block;
				margin-bottom: 0;
			}
			li:not(:first-child) {
				margin-left: 0px;
			}
			li:last-child {
				margin-right: 0px;
			}

			li a {
				display: block;
				outline: none;
				color: $white-color;
				text-transform: uppercase;
				text-decoration: none;
				font-size: 14px;
				letter-spacing: 1.2px;
				font-weight: 600;
				padding: 20px 15px;
				border-bottom: 4px solid transparent;
				@include transtion-time(.2s);

				&:hover{
					border-bottom: 4px solid red;
				}
			}
		}
	}
// Nav-rigth-icon
	.nav-icon{
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 60px;
	}
	.nav-icon i{
		width: 35px;
		height: 35px;
		background-color: $them-color;
		text-align: center;
		line-height: 35px;
		border-radius: 50px;
		color: #44B87A;
		font-size: 16px;
	}
}

@media screen and (max-width: 991px) {
	.logo {
		padding: 0;
	}
	.nav-wrapper {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		background: #fff;
		opacity: 0;
		transition: all 0.2s ease;
	}

	.nav-wrapper ul {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 40%;
		background-color: $secend-color;
	}

	.nav-wrapper ul li {
		display: block;
		float: none;
		width: 100%;
		text-align: right;
		margin-bottom: 10px;
	}
	nav ul li a {
		border-bottom: none;
	}
	nav ul li a:hover {
		border-bottom: none;
	}
	.nav-wrapper ul li:nth-child(1) a {
		transition-delay: 0.2s;
	}

	.nav-wrapper ul li:nth-child(2) a {
		transition-delay: 0.3s;
	}

	.nav-wrapper ul li:nth-child(3) a {
		transition-delay: 0.4s;
	}

	.nav-wrapper ul li:nth-child(4) a {
		transition-delay: 0.5s;
	}

	.nav-wrapper ul li:not(:first-child) {
		margin-left: 0;
	}

	.nav-wrapper ul li a {
		padding: 10px 24px;
		opacity: 0;
		color: #000;
		font-size: 14px;
		font-weight: 600;
		letter-spacing: 1.2px;
		transform: translateX(-20px);
		transition: all 0.2s ease;
	}

	.nav-btn {
		position: fixed;
		right: 10px;
		top: 10px;
		display: block;
		width: 48px;
		height: 48px;
		cursor: pointer;
		z-index: 9999;
		border-radius: 50%;
	}

	.nav-btn i {
		display: block;
		width: 20px;
		height: 2px;
		background: $white-color;
		border-radius: 2px;
		margin-left: 14px;
	}

	.nav-btn i:nth-child(1) {
		margin-top: 16px;
	}

	.nav-btn i:nth-child(2) {
		margin-top: 4px;
		opacity: 1;
	}

	.nav-btn i:nth-child(3) {
		margin-top: 4px;
	}
}

#nav:checked + .nav-btn {
	transform: rotate(45deg);
}

#nav:checked + .nav-btn i {
	background: $secend-color;
	transition: transform 0.2s ease;
}

#nav:checked + .nav-btn i:nth-child(1) {
	transform: translateY(6px) rotate(180deg);
}

#nav:checked + .nav-btn i:nth-child(2) {
	opacity: 0;
}

#nav:checked + .nav-btn i:nth-child(3) {
	transform: translateY(-6px) rotate(90deg);
}

#nav:checked ~ .nav-wrapper {
	z-index: 9990;
	opacity: 1;
}

#nav:checked ~ .nav-wrapper ul li a {
	opacity: 1;
	transform: translateX(0);
	text-align: center;
}

.hidden {
	display: none;
}

// Eend-Nav

//Start Baner-secton

#baner-secton{
	.baner-area{
		background-size: cover;
		background-position: center;
		width: 100%;
		min-height: 100vh;
	}
	.baner-item{
		text-align: center;
		height: auto;

		h1{
			color: $white-color;
		}
		h2{
			color: $white-color;
		}
		.btn{
			background-color: $them-color;
			color: $white-color;
			padding: 15px 50px 15px 50px;
			border-radius: 50px;
			text-transform: capitalize;
			
			&:focus{
				outline-color: none;
			}

		}
	}
}

// start-our-story-section

#our-story-sectoin{
	padding: $defult-py;
	
	.our-story-area{
		background-size: cover;
		background-position: center;
		width: 100%;
		min-height: 650px;

		.our-story-images img{
			width: 100%;
			min-height: 440px;
			background-color: orange;
			text-align: center;
		}
	}
}