
*{
	margin: 0;
	padding: 0;
}
@import url('https://fonts.googleapis.com/css?family=Great+Vibes|Poppins:300,400,500,600,700');

*,
*:after,
*:before {
	box-sizing: inherit
}

body, html {
	box-sizing: border-box;
}

body {
	font-family: $them-font;
	font-weight: normal;
	font-style: normal;
	background-color: $white-color;
}

i,
fab,
fas,
far {
	font-family: FontAwesome;
}

a {
	color: #9b4dca;
	text-decoration: none
}
a:focus,
a:hover {
	color: $secend-color;
	text-decoration: none;
}
a:visited:hover {
  color: transparent;
}

a:focus,
.button:focus{
	text-decoration: none;
	outline: none;
}
dl,
ol,
ul {
	list-style-type: none;
	margin: 0;
	padding: 0
}
hr {
	border: 0;
	border-top: 0.1rem solid #f4f5f6;
	margin: 3.0rem 0
}
p, li, a, pre, bdi, bdo, textarea, lable{
	line-height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 300;
	letter-spacing: -.1rem;
	margin-bottom: 2.0rem;
	margin-top: 0
}

h1 {
	font-size: 32px;
	line-height: 1.2
}

h2 {
	font-size: 30px;
	line-height: 1.25
}

h3 {
	font-size: 26px;
	line-height: 1.3
}

h4 {
	font-size: 22px;
	line-height: 1.35
}

h5 {
	font-size: 20px;
	line-height: 1.5
}

h6 {
	font-size: 18px;
	line-height: 1.4
}
p{
	font-size: 14px;
}
img {
	max-width: 100%
}

.clearfix:after {
	clear: both;
	content: ' ';
	display: table
}

.float-left {
	float: left
}

.float-right {
	float: right
}

